<template>

    <div>
        <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <!--  -->
        <el-card>
            <el-row :gutter="25">
                <el-col :span="3">
                    <el-dropdown split-button type="primary" @command="handleCommand">
                        {{queryInfo.type}}
                        <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item command="1"></el-dropdown-item>
                            <el-dropdown-item command="2"></el-dropdown-item>
                            <el-dropdown-item command="3"></el-dropdown-item>
                            <el-dropdown-item command="4"></el-dropdown-item>
                        </el-dropdown-menu>
                    </el-dropdown>
                </el-col>
                <el-col :span="3">
                    <el-dropdown split-button type="primary" @command="handleCommand">
                        {{queryInfo.xid}}
                        <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item command="5"></el-dropdown-item>
                            <el-dropdown-item command="6"></el-dropdown-item>
                        </el-dropdown-menu>
                    </el-dropdown>
                </el-col>
                <el-col :span="3">
                    <el-button  type="success" round @click="seacher"></el-button>
                </el-col>
                <el-col :span="2">
                    <el-button  type="success" v-if="root==1" round @click="openaddshow"></el-button>
                </el-col>
            </el-row>
            <div>
                <!-- -->
                <el-dialog title="" :visible.sync="descVisible">
                    <el-form ref="form"  :inline="true" :model="form" label-width="80px" class="demo-form-inline">
                        <el-form-item label=":">
                            <el-input v-model="form.vaccid"></el-input>
                        </el-form-item>
                        <el-form-item label=":">
                            <el-input v-model="form.name"></el-input>
                        </el-form-item>
                        <el-form-item label=":">
                            <el-input v-model="form.type"></el-input>
                        </el-form-item>
                        <el-form-item label=":">
                            <el-input v-model="form.coldroomid"></el-input>
                        </el-form-item>
                        <el-form-item label=":">
                            <el-input v-model="form.temp2"></el-input>
                        </el-form-item>
                        <el-form-item label=":">
                            <el-input v-model="form.temp2"></el-input>
                        </el-form-item>
                        <el-form-item label=":">
                            <el-col :span="11">
                                <el-date-picker type="date" placeholder="" disabled v-model="form.data1" style="width: 100%;"></el-date-picker>
                            </el-col>
                            <el-col class="line" :span="2">-</el-col>
                            <el-col :span="11">
                                <el-date-picker type="date" placeholder="" disabled v-model="form.data2" style="width: 100%;"></el-date-picker>
                            </el-col>
                        </el-form-item>
                        <el-form-item label=":">
                            <el-input v-model="form.shop"></el-input>
                        </el-form-item>
                        <el-form-item label=":">
                            <el-input v-model="form.intoroomtime"></el-input>
                        </el-form-item>
                        <el-form-item label="id:">
                            <el-input v-model="form.rootid"></el-input>
                        </el-form-item>
                        <el-form-item label=":">
                            <el-input v-model="form.guige"></el-input>
                        </el-form-item>
                        <el-form-item label=":">
                            <el-input v-model="form.price"></el-input>
                        </el-form-item>
                        <el-form-item label=":">
                            <el-input v-model="form.desction"></el-input>
                        </el-form-item>
                        <el-form-item label=":">
                            <el-input v-model="form.canperson"></el-input>
                        </el-form-item>
                        <el-form-item label=":">
                            <el-input v-model="form.body"></el-input>
                        </el-form-item>
                        <el-form-item label=":">
                            <el-input v-model="form.way"></el-input>
                        </el-form-item>
                        <el-form-item label=":">
                            <el-input v-model="form.usefultime"></el-input>
                        </el-form-item>
                        <el-form-item label=":">
                            <el-input v-model="form.warn"></el-input>
                        </el-form-item>
                        <el-form-item>
                            <el-button type="primary" @click="updateVacc"></el-button>
                            <el-button @click="descVisible=false"></el-button>
                        </el-form-item>
                    </el-form>
                </el-dialog>
            </div>

            <div>
                <!---->
                <el-dialog title="" :visible.sync="addVisible">
                    <el-form ref="form"  :rule="addFormRules"  :inline="true" :model="form" label-width="80px" class="demo-form-inline">
                        <el-form-item label=":">
                            <el-input v-model="form.vaccid"></el-input>
                        </el-form-item>
                        <el-form-item label=":">
                            <el-input v-model="form.name"></el-input>
                        </el-form-item>
                        <el-form-item label=":">
                            <el-input v-model="form.type"></el-input>
                        </el-form-item>
                        <el-form-item label=":">
                            <el-input v-model="form.coldroomid"></el-input>
                        </el-form-item>
                        <el-form-item label=":">
                            <el-input v-model="form.temp1"></el-input>
                        </el-form-item>
                        <el-form-item label=":">
                            <el-input v-model="form.temp2"></el-input>
                        </el-form-item>
                        <el-form-item label=":">
                            <el-col :span="11">
                                <el-date-picker type="date" placeholder="" v-model="form.createtime" style="width: 100%;"></el-date-picker>
                            </el-col>
                            <el-col class="line" :span="2">-</el-col>
                            <el-col :span="11">
                                <el-date-picker type="date" placeholder="" v-model="form.keep" style="width: 100%;"></el-date-picker>
                            </el-col>
                        </el-form-item>
                        <el-form-item label=":">
                            <el-input v-model="form.shop"></el-input>
                        </el-form-item>
                        <el-form-item label=":">
                            <el-input v-model="form.intoroomtime"></el-input>
                        </el-form-item>
                        <el-form-item label="id:">
                            <el-input v-model="form.rootid"></el-input>
                        </el-form-item>
                        <el-form-item label=":">
                            <el-input v-model="form.guige"></el-input>
                        </el-form-item>
                        <el-form-item label=":">
                            <el-input v-model="form.price"></el-input>
                        </el-form-item>
                        <el-form-item label=":">
                            <el-input v-model="form.desction"></el-input>
                        </el-form-item>
                        <el-form-item label=":">
                            <el-input v-model="form.canperson"></el-input>
                        </el-form-item>
                        <el-form-item label=":">
                            <el-input v-model="form.body"></el-input>
                        </el-form-item>
                        <el-form-item label=":">
                            <el-input v-model="form.way"></el-input>
                        </el-form-item>
                        <el-form-item label=":">
                            <el-input v-model="form.usefultime"></el-input>
                        </el-form-item>
                        <el-form-item label=":">
                            <el-input v-model="form.warn"></el-input>
                        </el-form-item>
                        <el-form-item>
                            <el-button type="primary" @click="addVacc"></el-button>
                            <el-button @click="addVisible=false"></el-button>
                        </el-form-item>
                    </el-form>
                </el-dialog>
            </div>
            <!--  -->
            <el-table :data="vacclist" border stripe height="500px">
                <el-table-column label=""   width="80px" prop="xid"></el-table-column>
                <el-table-column label="" width="80px" prop="type"></el-table-column>
                <el-table-column label="" width="140px" prop="subjectname"></el-table-column>
                <el-table-column label="" width="120px"  prop="school_group"></el-table-column>
                <el-table-column label=""  width="100px"  prop="subjecttype"></el-table-column>
                <el-table-column label=":" width="120px" prop="teacherid"></el-table-column>
                <el-table-column label=":" width="120px" prop="xuefen"></el-table-column>
                <el-table-column label=":" width="120px" prop="jidian"></el-table-column>
                <el-table-column label=":" width="120px" prop="grade"></el-table-column>
                <el-table-column label=":" width="120px" prop="ispass"></el-table-column>
                <el-table-column label="" v-if="root==1" width="250px" header-align="center">
                    <template slot-scope="scope">
                        <!--  -->
                        <el-button-group>
                            <el-tooltip effect="dark" content="" placement="top-start" :enterable="false">
                                <el-button type="danger"  size="mini" @click="deleteVacc(scope.row.vaccid)"  icon="el-icon-warning"></el-button>
                            </el-tooltip>
                            <el-button type="success"  size="mini" @click="isback(scope.row)"  icon="el-icon-edit"></el-button>
                            <el-button type="primary"  size="mini" @click="isout(scope.row)"  icon="el-icon-edit"></el-button>
                        </el-button-group>
                    </template>
                </el-table-column>
            </el-table>
            <!--  -->
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="queryInfo.pageNum"
                    :page-sizes="[10, 20, 40, 80]"
                    :page-size="queryInfo.pageSize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="total"
            ></el-pagination>
        </el-card>
    </div>
</template>
<script>
    export default {
        data() {
            return {
                // 
                root:0,//
                queryInfo: { //
                    query: "",
                    pageNum: 1,
                    pageSize: 20,
                    type:'',
                    xid:""
                },
                vacclist: [],// 
                total: 0, // 
                addDialogVisible: false ,// 
                descVisible:false,//
                // /
                editDialogVisible:false,
                addVisible:false,//
                //
                form:{
                    id: '',
                    vaccid: '',
                    name: '',
                    type: '',
                    temp1: '',
                    temp2: '',
                    coldroomid: '',
                    createtime: '',
                    keep: '',
                    shop: '',
                    intoroomtime: '',
                    rootid: '',
                    guige: '',
                    price: '',
                    desction: '',
                    canperson: '',
                    body: '',
                    way: '',
                    usefultime: '',
                    warn: '',
                    isback: '',
                    isout: '',
                    data1:'',
                    data2:''
                },




            };
        },
        created() {
            this.getVaccList();
        },
        methods: {
            handleCommand(command) {
                if(command==1){
                    this.queryInfo.type=""
                }
                if(command==2){
                    this.queryInfo.type=""
                }
                if(command==3){
                    this.queryInfo.type=""
                }
                if(command==4){
                    this.queryInfo.type=""
                }
                if(command==5){
                    this.queryInfo.xid=""
                }
                if(command==6){
                    this.queryInfo.xid=""
                }
            },
            async getVaccList() {
                // get
                const { data: res } = await this.$http.get("api/subject/getsubject?id="+window.sessionStorage.getItem("id"));
                this.vacclist = res.data; // 
                this.total = res.numbers; // 
                console.log(this.vacclist)
            },

            // pageSize
            handleSizeChange(newSize) {
                this.queryInfo.pageSize = newSize;
                this.getVaccList(); // 
            },
            // pageNum
            handleCurrentChange(newPage) {
                this.queryInfo.pageNum = newPage;
                this.getVaccList();// 
            },
            // flowStateChanged(scope.row)
            async flowStateChanged(flewinfo) {
                const { data: res } = await this.$http.put(
                    `flewError?id=${flewinfo.id}&error=${flewinfo.error}`
                );
                if (res != "success") {
                    flewinfo.error = !flewinfo.error;
                    return this.$message.error("");
                }
                this.$message.success("");
            },
            // 
            addDialogClosed(){
                this.$refs.addFormRef.resetFields();// 
            },
            openaddshow(){
                this.addVisible = true;
                /*         this.form.id = "";this.form.name = "";this.form.vaccid = "";
                         this.form.temp1 = "";this.form.temp2 = "";this.form.type = "";
                         this.form.createtime = "";this.form.keep = "";this.form.data1 = "";
                         this.form.data2 = "";this.form.desction = "";this.form.shop = "";
                         this.form.intoroomtime = "";this.form.warn = "";this.form.usefultime = "";this.form.price = "";
                         this.form.body = "";this.form.canperson = "";this.form.guige = "";
                         this.form.rootid = "";this.form.way = "";this.form.coldroomid = "";*/
            },
            //
            async seacher() {
                var arr =[];
                this.vacclist = arr;
                const {data: res} = await this.$http.get("api/subject/getselectCheck?id=" + window.sessionStorage.getItem("id") +
                    "&type=" + this.queryInfo.type + "&xid=" + this.queryInfo.xid);
                this.vacclist = res.data; // 
                this.total = res.numbers; // 
                console.log(this.vacclist)
            },
            // 
            async  addVacc() {
                // 
                const { data: res } = await this.$http.post("api/vacc/addvaccine",this.form);
                if (res != "success") {
                    return this.$message.error("");
                }
                this.$message.success("");
                //
                this.addVisible = false;
                this.getVaccList();
            },



            addperson(){
                this.addDialogVisible = true;
            },
            // 
            editDialogClosed(){
                this.$refs.editFormRef.resetFields();
            },
            // 
            async deleteVacc(id){
                // 
                const confirmResult = await this.$confirm(', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).catch(err => err)
                // confirm  cancel
                if(confirmResult!='confirm'){
                    return this.$message.info("");
                }
                const {data:res} = await this.$http.get("api/vacc/delectofvaccid?vaccid="+id);
                if (res == "no") {
                    return this.$message.error(""+id+",");
                }else if(res=='success'){
                    this.$message.success("");
                    this.getVaccList();
                }else{
                    this.$message.error("");
                }
            },
            async isback(row){
                const confirmResult = await this.$confirm('?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).catch(err => err)
                // confirm  cancel
                if(confirmResult!='confirm'){
                    return this.$message.info("");
                }
                const {data:res} = await this.$http.post("api/vacc/isback",row);
                if (res == "error") {
                    return this.$message.error(""+id+"");
                }else if(res=='success'){
                    this.$message.success("");
                    this.getVaccList();
                }else{
                    this.$message.error("");
                }
            },
            //
            async isout(row){
                const confirmResult = await this.$confirm('?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).catch(err => err)
                // confirm  cancel
                if(confirmResult!='confirm'){
                    return this.$message.info("");
                }
                const {data:res} = await this.$http.post("api/vacc/isout",row);
                if (res == "error") {
                    return this.$message.error(""+id+"");
                }else if(res=='success'){
                    this.$message.success("");
                    this.getVaccList();
                }else{
                    this.$message.error("");
                }
            },
            //
            async getvaccdataofvaccid(vaccid){
                this.descVisible = true;
                const { data: res } = await this.$http.get("api/vacc/getdataofvaccid?vaccid="+vaccid);
                if(res=="no"){
                    this.$message.success("")
                }else if(res=="error"){
                    this.$message.error("");
                }else{
                    this.form.id  = res.id;this.form.name = res.name;this.form.vaccid = res.vaccid;
                    this.form.temp1 =res.temp1;this.form.temp2 = res.temp2;this.form.type = res.type;
                    this.form.createtime = res.createtime; this.form.keep = res.keep;
                    this.form.data1 = res.data1;this.form.data2 = res.data2;
                    this.form.desction = res.desction;this.form.shop = res.shop;this.form.intoroomtime = res.intoroomtime;
                    this.form.warn = res.warn;this.form.usefultime = res.usefultime;this.form.price = res.price;
                    this.form.body = res.body;this.form.canperson = res.canperson;this.form.guige = res.guige;
                    this.form.rootid=res.rootid;this.form.way = res.way;this.form.coldroomid = res.coldroomid;
                }
                console.log(this.from)

            },
            //
            async updateVacc(){
                const { data: res } = await this.$http.post("api/vacc/updatevacc",this.form);
                if(res=="success"){
                    this.$message.success("");
                    this.descVisible = false;
                    this.getVaccList();
                }else{
                    this.$message.error("");
                }
            },

            //
            async getdataformname(){
                const {data:res} = await this.$http.post("api/vacc/getdataofname",this.queryInfo);
                if(res=="error"){
                    this.$message.error("");
                }else{
                    this.vacclist = res.data;
                    this.total = res.number;
                }
            },
            //
            async applyIsdelectChanged(row){
                const { data: res } = await this.$http.get("api/user/delect?id="+row.id+"&&isdelect="+row.isdelect);
                if(res=="success"){
                    this.$message.success(""+res)
                }else{
                    this.$message.error(""+res)
                }
            }
        }
    };
</script>
<style lang="less" scoped>
    .butt1{
        width: 40px;
        height: 25px;
    }
</style>
