<script>
  import * as echarts from 'echarts';
  export default {
    data () {
      return {
        studentadmin:false,//
        teacheradmin:false,//
        addSuject:false,//
        quxiao:false,
        setuan:false,//
        addactionwin:false,//
        publicMoney:false,//
