          id:0,
          createuser:"",
          desction:"",
          name:""
        },
        assoc:[],//
        dialogVisible:false,
        queryInfo: {
          query: "",
          pageNum: 1,
          pageSize: 4,

        },
        inputVisible: false,
        inputValue: '',
        reverse: true,
        actionform:{
          actionUser: "",
          actiontime: "",
          starttime: "",
          endtime: "",
          jifen: "",
          limitnum: 0,
          title: ""
        },
        activities: [{
             title:"dsnsdnd",
             starttime: "2022-4-23 1000",
             endtime: "2022-4-23 1100",
        }],
        labelPosition: 'right',
        formLabelAlign: {
          name: '',
