<template>
    <div class="success_background">
        <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <div style="width: 65%">
            <el-tabs type="border-card">
                <el-tab-pane label="">
                    <el-row :gutter="25">
                        <el-col :span="8">
                            <el-form ref="form" :inline="true" :model="acction" label-width="60px" size="mini">
                                <el-form-item label=":" label-width="200">
                                    <el-select v-model="acction.name" placeholder="">
                                        <div v-for="item in acctionlist">
                                            <el-option :label="item.title" :value="item.title"></el-option>
                                        </div>
                                    </el-select>
                                </el-form-item>
                                </el-form>
                        </el-col>
                        <el-col :span="5">
                            <el-button type="success" @click="getallSignlog" size="mini"></el-button>
                            <el-button type="danger" @click="addSign" size="mini"></el-button>
                        </el-col>
                    </el-row>
                    <el-table :data="tableData" border style="width: 100%">
                        <el-table-column fixed prop="id" label="" width="150"></el-table-column>
                        <el-table-column fixed prop="major" label="" width="170"></el-table-column>
                        <el-table-column fixed prop="local" label="" width="150">
                            <template slot-scope="scope">
                                <el-popover placement="top-start" title="" width="100" trigger="hover" effect="dark" >{{scope.row.local}}
                                    <span v-model="scope.row.local" slot="reference" style="color:blue">{{scope.row.local| ellipsis}}</span>
                                </el-popover>
                            </template>
                        </el-table-column>
                        <el-table-column fixed prop="signtime" label="" width="170"></el-table-column>
                        <el-table-column fixed prop="uname" label="" width="150"></el-table-column>
                        <el-table-column fixed prop="cardid" label="" width="150"></el-table-column>
                        <el-table-column
                                fixed="right"
                                label=""
                                width="100">
                            <template slot-scope="scope">
                                <el-button @click="getdel(scope.row)" type="text" size="small"></el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                    <!--  -->
                    <el-pagination
                            @size-change="handleSizeChange"
                            @current-change="handleCurrentChange"
                            :current-page="queryInfo.pageNum"
                            :page-sizes="[10, 20, 40, 80]"
                            :page-size="queryInfo.pageSize"
                            layout="total, sizes, prev, pager, next, jumper"
                            :total="total"
                    ></el-pagination>
                    <el-dialog title=""   :visible.sync="dialogFormVisible"   width="30%">
                        <el-form :model="form">
                            <el-form-item label="" :label-width="200">
                                <el-input v-model="form.uname" autocomplete="off"></el-input>
                            </el-form-item>
                            <el-form-item label="" :label-width="200">
                                <el-input v-model="form.cardid" autocomplete="off"></el-input>
                            </el-form-item>
                            <el-form-item label="" :label-width="200">
                                <el-input v-model="form.major"  autocomplete="off"></el-input>
                            </el-form-item>
                            <el-form-item label="" :label-width="200">
                                <el-input v-model="form.signtime" disabled autocomplete="off"></el-input>
                            </el-form-item>
                            <el-form-item label="" :label-width="200">
                                <el-input type="date" v-model="form.addsigntime" autocomplete="off"></el-input>
                            </el-form-item>
                        </el-form>
                        <div slot="footer" class="dialog-footer">
                            <el-button @click="dialogFormVisible = false"> </el-button>
                            <el-button type="primary" @click="updateSign"> </el-button>
                        </div>
                    </el-dialog>
                </el-tab-pane>
            </el-tabs>
        </div>


    </div>
</template>

<style lang="less" scoped>
    .success_massage{
        width: 300px;
        margin: 0 40%;
        background-color: aqua;
        font-size: 30px;
        text-align: center;
    }
    .go_home{
        margin-top:15px;
        margin-bottom: 10px;
    }
</style>

<script>
    export default {
        data() {
            return {
                num: 1,
                acction:{
                    name:"",
                },
                acctionlist:[],
                queryInfo: {
                    query: "",
                    pageNum: 1,
                    pageSize: 20,
                    value:"",
                },

                queryInfo1: {
                    query: "",
                    pageNum: 1,
                    pageSize: 20,
                },

                dialogFormVisible: false,
                dialogFormVisible1:false,
                total: 0, // 
                total1:0,
                tableData: [],
                form:{
                     addsigntime: "",
                     aid: 0,
                     cardid: "",
                     isdelect: 0,
                     local: "",
                     major: "",
                     signtime: "",
                     uname: ""
                },
                forms:{

                },
            }
        },
        created() {
            this.acctionlists();
        },
        methods: {
            //
            // pageSize
            handleSizeChange(newSize) {
                this.queryInfo.pageSize = newSize;
                this.getVaccloglist(); // 
                this.getUserlist();
            },
            // pageNum
            handleCurrentChange(newPage) {
                this.queryInfo.pageNum = newPage;
                this.getVaccloglist();// 
            },

            //
            async  getallSignlog(){
                for (let i = 0; i < this.acctionlist.length; i++) {
                    if (this.acctionlist[i].title == this.acction.name) {
                        this.queryInfo.id=this.acctionlist[i].id
                    }
                }
                const {data:res} = await this.$http.post("api/Sign/getSignlist",this.queryInfo);
                if(res.code==1){
                    this.tableData = res.data;
                    this.total = res.number;
                }else{
                    this.$message.error(res.msg)
                }
            },
            //
            async  acctionlists(){
                const {data:res} = await this.$http.get("api/Action/getActionall");
                if(res.code==1){
                    this.acctionlist = res.data;
                }else{
                    this.$message.error(res.msg)
                }
            },

            async  getdel(row){
                const confirmResult = await this.$confirm('?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).catch(err => err)
                // confirm  cancel
                if (confirmResult != 'confirm') {
                    return this.$message.info("");
                }
                const {data:res} = await this.$http.get("api/Sign/dellog?id="+row.id);
                if(res.code==1){
                    this.$message.success("")
                    await this.getallSignlog();
                }else{
                    this.$message.error("")
                }
            },

            getAction(row){
                this.dialogFormVisible = true;
                this.form = row;
            },
            //
            async isupdate() {
                const {data: res} = await this.$http.post("api/Action/updateAction",this.form);
                if (res.code==1) {
                    this.$message.success(res.msg)
                    this.getVaccloglist();
                } else {
                    this.$message.error(res.msg)
                }
            },

            //
            addSign(){
                this.dialogFormVisible = true;
            },

            //
            async updateSign() {
                if(this.acction.name==""){
                    this.$message.error("")
                    return ;
                }
                for (let i = 0; i < this.acctionlist.length; i++) {
                    if (this.acctionlist[i].title == this.acction.name) {
                        this.form.aid = this.acctionlist[i].id
                    }
                }
                const {data: res} = await this.$http.post("api/Sign/addSign", this.form);
                if (res.code == 1) {
                    this.$message.success("")
                    this.dialogFormVisible = false
                    this.getallSignlog();
                } else {
                    this.$message.error(res.msg)
                }

            },



        },
        //10
        filters:{
            ellipsis(value){
                if (!value) return '';
                if (value.length > 8) {
                    return value.slice(0,8) + '...'
                }
                return value
            }
        }
    }
</script>
