<template>
    <div class="success_background">
        <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <div style="width: 65%">
            <el-tabs type="border-card">
                <el-tab-pane label="">
                    <el-row :gutter="25">
                        <el-col :span="8">
                            <!--  -->
                            <el-input placeholder="" v-model="queryInfo.query" clearable @clear="getVaccloglist">
                                <el-button slot="append" icon="el-icon-search" @click="getVaccloglist"></el-button>
                            </el-input>
                        </el-col>
                    </el-row>
                    <el-table :data="tableData" border style="width: 100%">
                        <el-table-column fixed prop="actiontime" label="" width="150"></el-table-column>
                        <el-table-column fixed prop="title" label="" width="150">
                            <template slot-scope="scope">
                                <el-popover placement="top-start" title="" width="100" trigger="hover" effect="dark" >{{scope.row.title}}
                                    <span v-model="scope.row.title" slot="reference" style="color:blue">{{scope.row.title| ellipsis}}</span>
                                </el-popover>
                            </template>
                        </el-table-column>
                        <el-table-column fixed prop="actionUser" label="" width="150"></el-table-column>
                        <el-table-column fixed prop="starttime" label="" width="150"></el-table-column>
                        <el-table-column fixed prop="endtime" label="" width="150"></el-table-column>
                        <el-table-column fixed prop="limitnum" label="" width="150"></el-table-column>
                        <el-table-column fixed prop="jifen" label="" width="150"></el-table-column>
                        <el-table-column
                                fixed="right"
                                label=""
                                width="140">
                            <template slot-scope="scope">
                                <el-button @click="getCode(scope.row)" type="text" size="small"></el-button>
                                <el-button @click="getdel(scope.row)" type="text" size="small"></el-button>
                                <el-button @click="getAction(scope.row)" type="text" size="small"></el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                    <!--  -->
                    <el-pagination
                            @size-change="handleSizeChange"
                            @current-change="handleCurrentChange"
                            :current-page="queryInfo.pageNum"
                            :page-sizes="[10, 20, 40, 80]"
                            :page-size="queryInfo.pageSize"
                            layout="total, sizes, prev, pager, next, jumper"
                            :total="total"
                    ></el-pagination>
                    <el-dialog title=""   :visible.sync="dialogFormVisible"   width="30%">
                        <el-form :model="form">
                            <el-form-item label="" :label-width="formLabelWidth">
                                <el-input v-model="form.title" autocomplete="off"></el-input>
                            </el-form-item>
                            <el-form-item label="" :label-width="formLabelWidth">
                                <el-input v-model="form.starttime" autocomplete="off"></el-input>
                            </el-form-item>
                            <el-form-item label="" :label-width="formLabelWidth">
                                <el-input v-model="form.endtime" autocomplete="off"></el-input>
                            </el-form-item>
                            <el-form-item label="" :label-width="formLabelWidth">
                                <el-input v-model="form.jifen" autocomplete="off"></el-input>
                            </el-form-item>
                            <el-form-item label="" :label-width="formLabelWidth">
                                <el-input v-model="form.limitnum" autocomplete="off"></el-input>
                            </el-form-item>
                        </el-form>
                        <div slot="footer" class="dialog-footer">
                            <el-button @click="dialogFormVisible = false"> </el-button>
                            <el-button type="primary" @click="isupdate"> </el-button>
                        </div>
                    </el-dialog>
                    <el-dialog title=""   :visible.sync="qiandaoSign"   width="30%">
                        <el-form :model="form">
                            <el-form-item label="" label-width="200">
                                <el-input v-model="form.title" disabled autocomplete="off"></el-input>
                            </el-form-item>
                            <el-form-item label=":">
                                <el-select v-model="local" placeholder="">
                                    <div v-for="item in locallist">
                                        <el-option :label="item.localname" :value="item.localname"></el-option>
                                    </div>
                                </el-select>
                            </el-form-item>
                        </el-form>
                        <div slot="footer" class="dialog-footer">
                            <el-button @click="qiandaoSign = false"> </el-button>
                            <el-button type="primary" @click="createCode"> </el-button>
                        </div>
                    </el-dialog>
                </el-tab-pane>
                <el-tab-pane label="">
                    <el-row :gutter="25">
                        <el-col :span="8">
                            <!--  -->
                            <el-input placeholder="" v-model="queryInfo1.query" clearable @clear="getUserlist">
                                <el-button slot="append" icon="el-icon-search" @click="getUserlist"></el-button>
                            </el-input>
                        </el-col>
                    </el-row>
                    <el-table :data="Usertable" border style="width:85%">
                        <el-table-column fixed prop="id" label="" width="100"></el-table-column>
                        <el-table-column fixed prop="cardid" label="" width="150"></el-table-column>
                        <el-table-column prop="username" label="" width="120"></el-table-column>
                        <el-table-column prop="major" label="" width="180"></el-table-column>
                        <el-table-column prop="shool_group" label="" width="120"></el-table-column>
                        <el-table-column prop="role" label="" width="120"></el-table-column>
                        <el-table-column prop="grade" label="" width="80">
                            <template slot-scope="scope">
                                <span style="color: red;font-weight: 600;">{{scope.row.grade}}</span>
                            </template>
                        </el-table-column>
                        <el-table-column
                                fixed="right"
                                label=""
                                width="160">
                            <template slot-scope="scope">
                                <el-input-number v-model="scope.row.grade"  size="mini" :precision="2" :step="0.1" :max="100"></el-input-number>
                                <el-button @click="chagegrade(scope.row)" type="text" size="small"></el-button>
                                <el-button @click="openwin(scope.row)" type="text" size="small"></el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                    <div style="margin-left:35%">
                        <div class="block">
                            <el-pagination
                                    @size-change="handleSizeChange"
                                    @current-change="handleCurrentChange"
                                    :current-page="queryInfo1.pageNum"
                                    :page-sizes="[5,10,15]"
                                    :page-size="queryInfo1.pageSize"
                                    layout="total, sizes, prev, pager, next"
                                    :total="total1">
                            </el-pagination>
                        </div>
                    </div>
                    <el-dialog title=""   :visible.sync="dialogFormVisible1"   width="30%">
                        <el-form :model="forms">
                            <el-form-item label="" :label-width="formLabelWidth">
                                <el-input v-model="forms.username" autocomplete="off"></el-input>
                            </el-form-item>
                            <el-form-item label="" :label-width="formLabelWidth">
                                <el-input v-model="forms.major" autocomplete="off"></el-input>
                            </el-form-item>
                            <el-form-item label="" :label-width="formLabelWidth">
                                <el-input v-model="forms.password" autocomplete="off"></el-input>
                            </el-form-item>
                            <el-form-item label="" :label-width="formLabelWidth">
                                <el-input v-model="forms.shool_group" autocomplete="off"></el-input>
                            </el-form-item>
                            <el-form-item label="" :label-width="formLabelWidth">
                                <el-input v-model="forms.cardid" autocomplete="off"></el-input>
                            </el-form-item>
                        </el-form>
                        <div slot="footer" class="dialog-footer">
                            <el-button @click="dialogFormVisible1 = false"> </el-button>
                            <el-button type="primary" @click="isupdateUser"> </el-button>
                        </div>
                    </el-dialog>

                </el-tab-pane>
            </el-tabs>
        </div>


    </div>
</template>

<style lang="less" scoped>
    .success_massage{
        width: 300px;
        margin: 0 40%;
        background-color: aqua;
        font-size: 30px;
        text-align: center;
    }
    .go_home{
        margin-top:15px;
        margin-bottom: 10px;
    }
</style>

<script>
    export default {
        data() {
            return {
                num: 1,
                local:"",
                locallist:[],//
                queryInfo: {
                    query: "",
                    pageNum: 1,
                    pageSize: 20,
                },
                qiandaoSign:false,//
                queryInfo1: {
                    query: "",
                    pageNum: 1,
                    pageSize: 20,
                },

                dialogFormVisible: false,
                dialogFormVisible1:false,
                total: 0, // 
                total1:0,
                tableData: [],
                Usertable:[],
                form:{

                },
                forms:{

                },
            }
        },
        created() {
            this.getVaccloglist();
            this.getUserlist();
        },
        methods: {
            //
            // pageSize
            handleSizeChange(newSize) {
                this.queryInfo.pageSize = newSize;
                this.getVaccloglist(); // 
                this.getUserlist();
            },
            // pageNum
            handleCurrentChange(newPage) {
                this.queryInfo.pageNum = newPage;
                this.getVaccloglist();// 
            },
            async  getVaccloglist(){
                const {data:res} = await this.$http.post("api/Action/getselectPageadmin",this.queryInfo);
                if(res.code==1){
                    this.tableData = res.data;
                    this.total = res.number;
                    this.dialogFormVisible = false
                }else{
                    this.$message.error(res.msg)
                }
            },

            async  getUserlist(){
                const {data:res} = await this.$http.post("api/User/getUserlist",this.queryInfo1);
                if(res.code==1){
                    this.Usertable = res.data;
                    this.total1 = res.number;
                }else{
                    this.$message.error(res.msg)
                }
            },
            //
            getCode(row){
                this.qiandaoSign = true;
                this.form = row;
                this.locallists();
            },
            //
            async  locallists(){
                const {data:res} = await this.$http.get("api/Local/getAlllocal");
                if(res.code==1){
                    this.locallist = res.data;
                }else{
                    this.$message.error(res.msg)
                }
            },
           //
            async createCode() {
                //
                const code = {
                    lat2: "",
                    lng2: "",
                    actionid: ""
                };
                for (let i = 0; i < this.locallist.length; i++) {
                    if (this.locallist[i].localname == this.local) {
                        code.lat2 = this.locallist[i].lat2;
                        code.lng2 = this.locallist[i].lng2;
                        code.actionid = this.form.id;
                    }
                }

                const {data: res} = await this.$http.post("api/code/getcode", code);
                if (res!=null) {
                   this.$message.success("");
                    this.qiandaoSign = false;
                } else {
                    this.$message.error("");
                    this.qiandaoSign = false;
                }

            },

            async  getdel(row){
                if(row.actionUser!=window.sessionStorage.getItem("user")){
                    this.$message.error("")
                    return;
                }
                const confirmResult = await this.$confirm('?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).catch(err => err)
                // confirm  cancel
                if (confirmResult != 'confirm') {
                    return this.$message.info("");
                }
                const {data:res} = await this.$http.get("api/Action/deleteAction?id="+id);
                if(res=="success"){
                    this.$message.success("")
                    this.dialogFormVisible = false
                    this.getVaccloglist();
                }else{
                    this.$message.error("")
                }
            },

            getAction(row){
                this.dialogFormVisible = true;
                this.form = row;
            },
            //
            async isupdate() {
                const {data: res} = await this.$http.post("api/Action/updateAction",this.form);
                if (res.code==1) {
                    this.$message.success(res.msg)
                    this.getVaccloglist();
                } else {
                    this.$message.error(res.msg)
                }
            },

            //api/user/updateUser
            async chagegrade(row) {
                const {data: res} = await this.$http.post("api/user/updateUser", row);
                if (res == "success") {
                    this.$message.success("")
                    this.getUserlist();
                } else {
                    this.$message.error("")
                }

            },

            async openwin(row) {
                this.dialogFormVisible1 = true;
                this.forms = row;
            },
            async isupdateUser() {

                const {data: res} = await this.$http.post("api/user/updateUser", this.forms);
                if (res == "success") {
                    this.$message.success("")
                    this.dialogFormVisible1 = false;
                    this.getUserlist();
                } else {
                    this.$message.error("")
                }
            },

        },
        //10
        filters:{
            ellipsis(value){
                if (!value) return '';
                if (value.length > 8) {
                    return value.slice(0,8) + '...'
                }
                return value
            }
        }
    }
</script>
