            }
        },
        created() {
            this.getUserdatabyid();
            this.assocation();
            this.url = "http://localhost:9002/api/user/updateUserpic?id="+window.sessionStorage.getItem('id');
        },
        methods: {

            async  getUserdatabyid(){
                // post
