<template>
    <div>
        <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <el-row :gutter="20">
            <el-col :span="14"><div class="grid-content bg-purple">
                <el-tabs v-model="activeName" @tab-click="handleClick">
                    <el-tab-pane label="" name="first">
                        <el-card>
                            <!--  -->
                            <el-table :data="firstvacclist" height="480px" border stripe>
                                <el-table-column label="" width="90px" prop="name"></el-table-column>
                                <el-table-column label="" width="90px" prop="vaccid"></el-table-column>
                                <el-table-column label="" width="90px" prop="type"></el-table-column>
                                <el-table-column label="" width="80px" prop="usefultime"></el-table-column>
                                <el-table-column label="" width="80px" prop="shop"></el-table-column>
                                <el-table-column label="" width="120px" prop="coldroomid"></el-table-column>
                                <el-table-column label="" width="80px" prop="price"></el-table-column>
                            </el-table>
                            <!--  -->
                            <el-pagination
                                    @size-change="handleSizeChange"
                                    @current-change="handleCurrentChange"
                                    :current-page="queryInfo.pageNum"
                                    :page-sizes="[10, 20, 40, 80]"
                                    :page-size="queryInfo.pageSize"
                                    layout="total, sizes, prev, pager, next, jumper"
                                    :total="total"
                            ></el-pagination>
                        </el-card>
                    </el-tab-pane>
                    <el-tab-pane label="" name="second">
                        <el-card>
                            <!--  -->
                            <el-table :data="firstvacclist" height="480px" border stripe>
                                <el-table-column label="" width="90px" prop="name"></el-table-column>
                                <el-table-column label="" width="90px" prop="vaccid"></el-table-column>
                                <el-table-column label="" width="90px" prop="type"></el-table-column>
                                <el-table-column label="" width="80px" prop="usefultime"></el-table-column>
                                <el-table-column label="" width="80px" prop="shop"></el-table-column>
                                <el-table-column label="" width="120px" prop="coldroomid"></el-table-column>
                                <el-table-column label="" width="80px" prop="price"></el-table-column>
                            </el-table>
                            <!--  -->
                            <el-pagination
                                    @size-change="handleSizeChange"
                                    @current-change="handleCurrentChange"
                                    :current-page="queryInfo.pageNum"
                                    :page-sizes="[10, 20, 40, 80]"
                                    :page-size="queryInfo.pageSize"
                                    layout="total, sizes, prev, pager, next, jumper"
                                    :total="total"
                            ></el-pagination>
                        </el-card>
                    </el-tab-pane>
                </el-tabs>
            </div></el-col>
            <el-col :span="10"><div class="grid-content bg-purple">
                <el-card shadow="hover" style="margin-top:60px">
                 <div>
                     <div id="myechart" style="width: 500px;height: 500px;"></div>
                 </div>
                </el-card>
            </div></el-col>
        </el-row>
    </div>
</template>
<script>
   import * as echarts from 'echarts';//echarts
    export default {
        data() {
            return {
                queryInfo: {
                    query: "",
                    pageNum: 1,
                    pageSize: 20,
                    style:'',
                    roomid:0,
                },
                activeName: 'first',
                firstvacclist:[],
                total:0,
            };
        },
        created() {
            this.getIsoutdata();
            //this.getEchartdata();
        },
        mounted() {
           this.getEchartdata();
        },
        methods: {
            async  getIsoutdata(){
                // post

                const { data: res } = await this.$http.post("api/room/getVaccisout",this.queryInfo);
                this.firstvacclist = res.data; // 
                this.total = res.number; // 
            },
            // pageSize
            handleSizeChange(newSize) {
                this.queryInfo.pageSize = newSize;
                this.getIsoutdata(); // 
            },
            // pageNum
            handleCurrentChange(newPage) {
                this.queryInfo.pageNum = newPage;
                this.getIsoutdata();// 
            },
            handleClick(tab, event) {
                if(this.activeName=='first'){
                    this.queryInfo.query = ""
                }else{
                    this.queryInfo.query = ""
                }
                this.getIsoutdata();
            },

            //
          async  getEchartdata(){
                const { data: res } = await this.$http.post("api/room/echartdata"); //api/echartdata

                if(res!=null){
                    //,,,
                    var xdata = res[0].split(","); //resxdata
                    var ydata = res[1].split(","); //resydata
                }
                var chartDom = document.getElementById('myechart');
                var myChart = echarts.init(chartDom);
                var option;
                option = {
                    title: {
                        text: '',
                        subtext: ''
                    },
                    xAxis: {
                        type: 'category',
                        data: xdata,
                    },
                    yAxis: {
                        type: 'value'
                    },
                    series: [
                        {
                            data: ydata,
                            type: 'bar'
                        }
                    ]
                };
                option && myChart.setOption(option);
            },

        }
    };
</script>